<template>
  <div class="blogList">

    <div id="searchBox"
         class="searchBox"
         ref="searchBox">

      <slot name="input"></slot>

    </div>
    <div class="infinite-list-wrapper"
         style="overflow:auto;position: relative;">
      <!-- <loading></loading> -->
      <el-scrollbar>
        <ul class="list"
            v-infinite-scroll="load"
            infinite-scroll-disabled="disabled">
          <li v-for="blog in blogList"
              class="list-item"
              :key="blog.id">
            <div>
              <el-card shadow="hover">
                <div slot="header"
                     class="clearfix">
                  <router-link :to="{name:'blogInfo',query:{'blogId':blog.blogOnlyId}}"
                               class="link">
                    <div class="title">{{blog.blogTitle}}</div>
                    <div class="RightAuthorInfo">
                      <el-avatar class="image"
                                 src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                      <span>{{blog.name}}</span>
                    </div>
                  </router-link>

                </div>
                <!-- <div class="title">{{blog.blogTitle}}</div> -->
                <div class="content"
                     v-html="blog.blogContent"></div>
              </el-card>

            </div>

          </li>
        </ul>
        <p v-if="loading"
           style="color:#409EFF"><i class="el-icon-loading"></i>加载中</p>
        <p v-if="noMore">没有更多了</p>
      </el-scrollbar>

    </div>

  </div>
</template>



<script>
//  import loading from '../components/loading/loading'
import systemBlogType from '../components/systemBlogType/systemBlogType'
export default {
  name: 'blogList',
  data () {
    var disabledInit = this.infiniteDisable;
    if (!this.infiniteScroll) {
      this.disabledInit = true;
    }
    return {
      isActive: true,
      count: 10,
      loading: false,
      noMore: false,
      blogSearch: {
        search: '',
        index: 0,
        pagecount: 10,
        blogtypeId: '',
        type: 0,
        api_token: ''
      },
      total: 100,
      blogList: this.data,
      disabled: disabledInit

      //系统类型
      //  systemBlogType: '',

    }
  },
  //['data', 'infiniteScroll', 'infiniteDisable']
  props: {
    data: {
      type: Array,
      default () {
        return []
      }
    },
    infiniteScroll: {
      type: Boolean,
      default () {
        return false
      }
    },
    infiniteDisable: {
      type: Boolean,
      default () {
        return true
      }
    }
  },
  components: {
    //  loading,
    systemBlogType
  },
  watch: {
    data: function (newValue, old) {
      this.blogList = newValue;
    }
  },

  mounted () {
    //this.load()
    //监听滚动条 searchbox移动到顶部时加上阴影
    // window.addEventListener('mousewheel', this.handleScroll, false)

  },
  methods: {
    load () {

      this.loading = true;
      //数据全部返回，则禁用无限滚动控件 callback:这次加载完成，所有数据加载完成
      this.$emit("infiniteFn", '', (nowComplate, allComplete) => {

        if (allComplete) {
          this.disabled = true;
          this.noMore = true;
        }
        if (nowComplate) {
          this.loading = false;
        }

      })
    },


    handleSelect () { },

    //处理滚动条
    // handleScroll () {
    //   // console.log(this.$refs.searchBox.offsetTop)
    //   // if (this.$refs.searchBox.offsetTop > 480) {
    //   //   this.$refs.searchBox.style.boxShadow = "0 2px 12px 0 rgba(0, 0, 0, 0.1)"
    //   // }
    //   // else {
    //   //   this.$refs.searchBox.style.boxShadow = "none"
    //   // }
    // }

  }
}
</script>

<style lang="less" scoped>
.blogList {
  float: left;
  width: 100%;

  overflow-y: scroll;
  background: #ffffff;
  //height: calc(100vh - 200px);
  .searchBox {
    width: 100%;
    min-width: 1200px;
    position: sticky;
    top: 0;
    height: 100px;
    line-height: 100px;
    background: #ffffff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    .input {
      float: left;
      //  width: 50%;
      min-width: 600px;
    }
    .span {
      float: left;
      min-width: 600px;

      .isActive {
        background: #ffffff;
        //  border-color: red;
        color: #409eff;
      }
      span {
        cursor: 123;
      }
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }
  .el-scrollbar {
    width: 100%;
    position: relative;
    height: calc(100vh - 350px);
    ul {
      // height: calc(100vh - 300px);
      li {
        padding: 20px;
        .el-card {
          /deep/ .link {
            display: flex;
            justify-content: space-between;
            color: #000;
            .title {
              text-align: left;
              font-weight: 700;
              cursor: pointer;
              color: #000;
            }
            .RightAuthorInfo {
              height: 40px;
              line-height: 40px;
              span {
                vertical-align: top;
              }
            }
          }
          .content {
            text-align: left;
            color: #606266;
          }
        }
      }
    }
  }
}
.blogList::-webkit-scrollbar {
  display: none;
}

/deep/ .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>


