<template>
  <!-- <div class="wrapper">
    <div class="leftMenu">
      <login></login>
      <leftMenu></leftMenu>
    </div>
   
    <div class="rightContent"
         id="rightContent">
      <div class="top"></div>
    </div>

    
    <blogList></blogList>
  </div> -->
  <el-container>
    <el-header>
      <login></login>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <leftMenu :menuItem="menuItem"></leftMenu>
      </el-aside>
      <el-main>
        <blogList :data="blogList"
                  :infiniteScroll="true"
                  @infiniteFn="getBlogList"
                  :infiniteDisable="infiniteDisable">

          <div slot="input">
            <label for="">标题：</label>
            <el-autocomplete v-model="blogSearch.search"
                             :fetch-suggestions="querySearchAsync"
                             placeholder="请输入内容"></el-autocomplete>
            <div style="width:200px;display:inline-block">
              <systemBlogType @select="selectSystemBlog"></systemBlogType>
            </div>
            <el-button icon="el-icon-search"
                       @click="search('search')"
                       plain></el-button>

            <div class="span">

              <el-button icon='el-icon-top'
                         plain
                         :class="{isActive:isActive}"
                         @click="
                   search('time')">按时间</el-button>
              <el-button icon='el-icon-top'
                         plain
                         :class="{isActive:!isActive}"
                         @click="search('hot')">按热度</el-button>
            </div>
          </div>

        </blogList>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
import leftMenu from '../components/leftMenu'
import login from './login.vue'
import blogList from '../components/blogList'
import systemBlogType from '../components/systemBlogType/systemBlogType'
export default {
  name: 'mainHome',
  components: {
    leftMenu,
    login,
    blogList,
    systemBlogType
  },
  mounted () {
    // console.log(this.$router)
  },
  data () {
    return {

      //左边菜单
      menuItem: [{
        name: '主页',
        link: '/home',
        Image: 'el-icon-s-home',
        index: 1
      },
      {
        name: '写博',
        link: '/writeBlog',
        Image: 'el-icon-edit',
        index: 2
      }, {
        name: '查博',
        link: '/searchBlog',
        Image: 'el-icon-search',
        index: 3
      }, {
        name: '收藏',
        link: '/writeBlog',
        Image: 'el-icon-star-off',
        index: 4
      }, {
        name: '我的',
        link: '/mine',
        Image: 'el-icon-user',
        index: 5
      }, {
        name: '大V',
        link: '/bigV',
        Image: 'el-icon-s-check',
        index: 5
      }],

      //博客列表
      blogList: [],
      blogSearch: {
        api_token: '',
        index: 0,
        pagecount: 10,
        search: ''
      },
      infiniteDisable: false,
      isActive: true,
    }
  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.blogSearch.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }

  },
  methods: {
    getBlogList (data, callback) {


      var query = this.blogSearch
      this.blogSearch.index++;
      this.$API.getBlogList(query).then(res => {

        if (query.index * query.pagecount >= res.data.blogNum) {
          this.infiniteDisable = true
          callback(true, true)
        }
        if (res.data.data) {
          res.data.data.forEach(element => {
            this.blogList.push(element)
          });
          callback(true, false)
        }

      }).catch(err => {
        console.log(err)
      })


    },
    //搜索框查询
    querySearchAsync (query, callback) {
      console.log(this.$data, this.$options.data())
      // console.log('query', query)
      this.$API.getBlogTitle({ api_token: this.blogSearch.api_token, title: query }).then(
        res => {
          console.log(res.data)
          //var data = [{ value: 1 }, { value: 2 }]
          callback(res.data.data)
        }
      )
    },
    //搜索按钮
    search (parm) {
      if (parm == 'time') {
        this.isActive = true
        this.blogSearch.type = 1
        this.blogSearch.index = 1;
      }
      else if (parm == 'hot') {
        this.isActive = false
        this.blogSearch.type = 0
        this.blogSearch.index = 1;
      }
      else if (parm == 'search') {
        this.blogSearch.index = 1;
      }
      this.$API.getBlogList(this.blogSearch).then(res => {
        this.total = res.data.blogNum
        this.blogList = res.data.data
      }).catch(err => {
        console.log(err)
      })

    },
    selectSystemBlog (data) {
      this.blogSearch.blogtypeId = data
    }
  }

}
</script>
<style lang="less" scoped>
.el-container {
  .el-header {
    height: 200px !important;
  }
  .el-container {
    .el-aside {
    }
    .el-main {
      min-width: 1200px;
    }
  }
}

@media screen and (max-width: 500px) {
  .rightContent {
    width: 100%;
  }
  .leftMenu {
    display: none;
  }
  .bottomMenu {
    display: block;
  }
}
</style>



