<template>
  <div>

    <el-select v-model="blogType"
               placeholder="请选择"
               @change='change'
               style="width:100%">
      <el-option v-for="item in systemBlogType"
                 :key="item.blogTypeOnlyId"
                 :label="item.name"
                 :value="item.blogTypeOnlyId">
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {

  data () {

    return {
      systemBlogType: '',
      api_token: '',
      blogType: ""

    }
  },

  mounted () {

    var a = JSON.parse(window.localStorage.getItem('BlogType'))
    if (a) {

      this.systemBlogType = a

      console.log(a)
    }
    else {

      if (window.sessionStorage.getItem('apiToken')) {
        this.api_token = window.sessionStorage.getItem('apiToken')
        console.log(this.api_token)
        this.getType();
      } else {
        this.$router.push('login')
      }

    }
  },
  methods: {
    change (data) {
      this.$emit('select', data)
    },
    getType () {
      this.$API.getBlogType({ 'api_token': this.api_token }).then(
        res => {

          this.systemBlogType = res.data.data

          window.localStorage.setItem('BlogType', JSON.stringify(this.systemBlogType));

        }
      ).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>


